/* _sass/_custom.scss */

// 1. Hero and Booking Widget
// --------------------------------------------------

.hero-carousel-container {
  position: relative;
  color: white;
}

#heroCarousel .carousel-item {
  height: 90vh;
  background-size: cover;
  background-position: center;
  position: relative;
}

// Dark overlay for better text readability on carousel images
#heroCarousel .carousel-item::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}

#heroCarousel .carousel-caption {
  bottom: 20%;
  z-index: 2;
}

.booking-widget {
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  max-width: 900px;
  background: #fff;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: 0 10px 30px rgba(0,0,0,0.15);
  z-index: 10;
  color: #333;
}

// 2. Amenities Section
// --------------------------------------------------
.amenity-item {
  text-align: center;
}
.amenity-icon {
  font-size: 2rem;
  color: #0d6efd;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
}
.amenity-item:hover .amenity-icon {
  transform: scale(1.2);
}


// 3. Testimonials Section
// --------------------------------------------------
.testimonials-section {
  position: relative;
}
.testimonial-content {
  padding: 2rem 5rem;
  text-align: center;
}
.testimonial-quote {
  font-size: 1.25rem;
  font-style: italic;
  color: #555;
}
.testimonial-author {
  margin-top: 1rem;
  font-weight: bold;
  color: #0d6efd;
}
#testimonialCarousel .carousel-control-prev-icon,
#testimonialCarousel .carousel-control-next-icon {
  background-color: #555;
  border-radius: 50%;
}


// 4. Member Benefits Section
// --------------------------------------------------
.member-benefit-icon {
  font-size: 3rem;
  color: #0d6efd;
  margin-bottom: 1.5rem;
}


// 5. Location Page "Nearby" Feature
// --------------------------------------------------
.nearby-container .nav-tabs .nav-link {
  color: #555;
}
.nearby-container .nav-tabs .nav-link.active {
  color: #0d6efd;
  font-weight: bold;
}
.nearby-container .list-group-item {
  border-left: none;
  border-right: none;
}
.nearby-container .fa-map-marker-alt, .nearby-container .fa-plane-departure {
  color: #0d6efd;
}


// 6. Beautified Footer
// --------------------------------------------------
.footer-section {
  position: relative;
}
.footer-content {
  position: relative;
  z-index: 2;
}
.footer-logo a {
  font-weight: 700;
}
.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
}
.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background: #0d6efd;
}
.footer-widget ul li {
  display: inline-block;
  width: 50%;
  margin-bottom: 12px;
}
.footer-widget ul li a:hover{
  color: #0d6efd;
}
.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
  text-decoration: none;
}
.subscribe-form {
  position: relative;
  overflow: hidden;
}
.subscribe-form input {
  width: 100%;
  padding: 14px 28px;
  background: #2E2E2E;
  border: 1px solid #2E2E2E;
  color: #fff;
}
.subscribe-form button {
  position: absolute;
  right: 0;
  background: #0d6efd;
  padding: 13px 20px;
  border: 1px solid #0d6efd;
  top: 0;
}
.subscribe-form button i {
  color: #fff;
  font-size: 22px;
  transform: rotate(-6deg);
}
.footer-social-icon span {
  color: #fff;
  display: block;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
}
.footer-social-icon a {
  color: #fff;
  font-size: 16px;
  margin-right: 15px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  display: inline-block;
}
.twitter-bg{
  background: #55ACEE;
}
.facebook-bg{
  background: #3B5998;
}
.instagram-bg {
  background: #d6249f;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
}
.copyright-area{
  background: #202020;
  padding: 25px 0;
}
.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}
.copyright-text p a{
  color: #0d6efd;
  text-decoration: none;
}
